<template >
    <div class="main1" >
        <div class="main">
            <div v-wechat-title="$route.meta.title"></div>
            <!-- 查看参与活动 -->
            <div class="typeTitle">
                <img src="../img/activity1.png" alt="">
                <span>参与活动</span>
            </div>
            <div class="content">
                <div class="activeWrap" v-for="(item,index) in isActivityList" :key="index"
                     @click="goDetail('/activitiesDetail?id='+item.activityId+'&agentId='+item.activityAgentId+'&agentName='+item.activityAgentName)">
                    <div class="activeTitle">活动名称：{{item.activityName}}</div>
                    <div class="activeContent">
                        <div class="left">
                            <img src="./img/activeText.png" alt="">
                        </div>
                        <div class="center">
                            <div class="">组织者：{{item.activityAgentName}}</div>
                            <div class="">活动时间：{{item.activityTime}}</div>
                            <div class="">活动地点：{{item.activityAddress}}</div>
                        </div>
                        <div class="right">
                            <img src="./img/rightJian.png">
                        </div>
                    </div>
                </div>
            </div>
            <div class="empty" v-if="activityList.length==0">
                <img src="../img/empty.png" alt="">
                <span>暂无参与活动</span>
            </div>
        </div>

        <div class="signUpOnline">
            <button @click="goBack()">返回</button>
        </div>

    </div>

</template>

<script>
    import request from '../../../../static/js/request';
    import result from '../../../../static/js/result';

    export default {
        data() {
            return {
                activityList: [],
                studentOrderList:[],
                arr:[],
                isActivityList:[]
            }
        },
        mounted() {

        },
        created() {

            this.getList();
            //this.getOrderList();
            this.getUserList();
        },
        methods: {
            goBack() {
                this.$router.go(-1)
            },
            goDetail(url) {
                this.$router.push(url);
            },
            async getOrderList(){
                this.activityList = [];
                let loading = weui.loading('loading', {
                    className: 'custom-classname'
                });
                let params  = {
                    access_token:this.$store.state.token,
                    orderUserId:this.$route.query.studentId
                }

                /* let headers = {
                     contentType:'application/x-www-form-urlencoded',
                     token:this.$store.state.token
                 }*/
                let getUserInfo = await request('/api/token/order/selectOrderInfoList',
                    'post',params,'application/x-www-form-urlencoded')
                 loading.hide()
                let info = result(getUserInfo)
                // loading.hide()
                if(!info) return;
                //console.log('获取电话号成功信息',getUserInfo)
                console.log(info)
                for(let item of info){
                    if(item.orderUserId == this.$route.query.studentId){
                        this.studentOrderList.push(item.orderActivityId)
                    }
                }
                console.log(this.studentOrderList)
                if(this.studentOrderList.length>0){
                    var array = [];
                    for (var i = 0; i < this.studentOrderList.length; i++) {
                        if (array .indexOf(this.studentOrderList[i]) === -1) {
                            array .push(this.studentOrderList[i])
                        }
                    }
                    console.log(array);
                    if(array.length>0){
                        for( let item of array ){
                            let params = {
                                activityId:item
                            };
                            let getListInfo = await request('/api/open/activity/selectActivityInfoById',
                                'post', params, 'application/x-www-form-urlencoded')
                            // loading.hide()
                            let res = result(getListInfo)

                            loading.hide()
                            console.log(res)
                            // loading.hide()
                            if (!res) return;
                            this.activityList.push(getListInfo.data)
                        }
                    }

                }


            },
            async getList() {
                let params = {};
                let getListInfo = await request('/api/open/activity/selectActivityInfoList',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let res = result(getListInfo)
                console.log(res)
                // loading.hide()
                if (!res) return;
                console.log('返回结果', getListInfo)
                //this.activityList = getListInfo.data;
                for(let item of getListInfo.data){
                    this.getUserList(item.activityId)
                }

            },
            async getUserList(activityId) {
                let params = {
                    activityId:activityId,
                    access_token:this.$store.state.token,
                };
                let getListInfo = await request('/api/token/activity/selectUserListApi',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let res = result(getListInfo)
                console.log(res)
                // loading.hide()
                if (!res) return;
                console.log('返回结果', getListInfo)
                for(let item1 of getListInfo.data){
                    if( this.$route.query.studentId == item1.studentId ){
                        this.arr.push(activityId)
                    }
                }
                this.$nextTick(()=>{
                    this.getSignActivity();
                })
                console.log('数组',this.arr)
            },
            async getSignActivity(){
                this.activityList = []
                if(this.arr.length>0){
                    for( let item of this.arr ){
                        let params = {
                            activityId:item
                        };
                        let getListInfo = await request('/api/open/activity/selectActivityInfoById',
                            'post', params, 'application/x-www-form-urlencoded')
                        // loading.hide()
                        let res = result(getListInfo)

                        console.log(res)
                        // loading.hide()
                        if (!res) return;
                        this.activityList.push(getListInfo.data)
                    }
                }

                this.isActivityList = this.unique(this.activityList);
                console.log('参与活动',this.isActivityList)
            },
            unique(arr) {
                const res = new Map();
                return arr.filter((arr) => !res.has(arr.activityId) && res.set(arr.activityId, 1));
            },
        }
    }
</script>

